* {
  margin: 0px;
  font-family: 'Roboto', sans-serif;
}

.input-tag {
  border: 1px solid black;
  padding: 10px 40px 10px 10px;
  width: 300px;
  font-size: 15px;
}

.login-box .heading {
  font-weight: bold;
  font-size: 20px;
  text-align: center;
  margin-bottom: 7px;
}
.login-box .sub-heading {
  text-align: center;
  margin-bottom: 50px;
}
.login-box {
  border: 1px solid grey;
  width: 60%;
  margin: auto;
  padding: 20px;
  box-shadow: 5px 5px 5px grey;
}

.login-form .label {
  grid-column: 1 / 2;
}
.login-form .inputundefined {
  grid-column: 2 / 4;
  padding: 5px;
  border-radius: 5px;
  align-self: start;
  border: 1px solid black;
}
.login-form .inputinvalid {
  border: 1px solid red;
  grid-column: 2 / 4;
  padding: 5px;
  border-radius: 5px;
  align-self: start;
}

.login-btn {
  grid-column: 2 / 3;
  margin-top: 20px;
  justify-self: start;
  background-color: rgb(20, 20, 90);
  color: rgb(235, 228, 228);
  font-weight: bold;
  width: 60px;
  padding: 8px;
  border-radius: 6px;
  box-shadow: 5px 5px 2px grey;
  font-size:15px;
}
.signup-btn-nav {
  text-decoration: none;
  background-color: rgb(20, 20, 90);
  color: rgb(235, 228, 228);
  padding: 7px;
  font-weight: bold;
  border-radius: 6px;
  box-shadow: 5px 5px 2px rgb(179, 125, 125);
  text-align: center;
  margin: auto 40px;
}
.signup-btn-nav:hover,
.login-btn:hover {
  color: rgb(36, 73, 36);
  background-color: rgb(134, 134, 184);
  box-shadow: 5px 5px 2px green;
}
.navbar .signIn {
  text-decoration: none;
  align-self: center;
  font-weight: bold;
}

.reset-pswd {
  display: block;
  text-align: center;
  font-weight: bold;
}
.reset-pswd:hover{
    color: rgb(77, 66, 20);
}
.acc-not-exist-msg {
  color: rgb(235, 48, 48);
  text-align: center;
  font-size: 17px;
  margin-bottom: 40px;
}

@media (min-width:768px) {
  .login-form {
    margin: 20px;
    display: grid;
    grid-column-gap: 20px;
    grid-row-gap: 15px;
    justify-content: center;
  }
  .login-form .inputundefined {
    grid-column: 2 / 4;
    padding: 5px;
    border-radius: 5px;
    align-self: start;
    border: 1px solid black;
  }
}

@media (min-width: 1024px) {
  .login-form {
    grid-template-columns: 200px 100px 200px;
  }
}

@media (max-width:375px) {
  .login-form .inputundefined {
    padding: 3px;
    border-radius: 5px;
    border: 1px solid black;
    margin-bottom: 20px;
    width: 160px;
  }
  .login-box .heading{
    font-size: 15px;
  }
  .login-box .sub-heading {
    font-size: 12px;
}
}

@media (max-width: 425px) {
  .login-form .label {
    font-size: 13px;
  }
  .login-form .inputundefined {
    padding: 5px;
    border-radius: 5px;
    border: 1px solid black;
    margin-bottom: 20px;
  }
  .login-btn {
    margin: 20px 50px;
  }
}